import axios from 'axios'
import { Toast } from 'vant'
const url = 'https://sit.sfrz.wsbs.shxga.gov.cn/yc-engine/liveness/silence/image'

const handleDetection = (imgUrl) => {
  const matchString = 'base64,'
  const index = imgUrl.indexOf(matchString)
  if (index === -1) return '匹配不到图片地址'
  const img = imgUrl.substr(index + matchString.length)
  console.log('img', img)
  return axios.post(url, {
    img,
    filterType: 24
  }).then(({ data }) => {
    if (data.isAlive) Toast.success('认证成功')
  })
}

export default handleDetection